.App {
  font-family: sans-serif;
  text-align: center;
}
.flash-card {
  font-size: 24px;
  line-height: 72px;
  width: 216px;
  border: 1px solid black;

  cursor: pointer;
  user-select: none; /*biztosítja, hogy ne lehessen kiválasztani a szöveget*/
}
.flash-card.front {
  background-color: grey;
}
.flash-card.back {
  background-color: #fff;
}
